.card {
  margin: 200px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 0;
}

.cardPersonal {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 30px 0;
}

.img {
  width: 150px;
  height: 150px;
  border-radius: 100%;
  object-fit: cover;
}

.imgFotik {
  width: 30px;
  height: 30px;
  position: absolute;
  bottom: 0;
  right: 20%;
  cursor: pointer;
}

.fotograph {
  position: relative;
  width: 150px;
  height: 150px;
}

.name {
  display: flex;
  align-items: center;
}

.pen {
  width: 15px;
  height: 15px;
  margin: 0 0 0 10px;
  cursor: pointer;
}

.email {
  color: grey;
  font-size: 15px;
  margin: 5px 0 15px 0;
  text-decoration: none;
}

.back {
  color: black;
  text-decoration: none;
  margin: 50px auto 0 auto;
  width: 80%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
