.row {
  transform: rotateX(180deg);
  transition: ease 0.7s;
}

.buton {
  display: flex;
  justify-content: center;
}

.spanZero {
  font-weight: bold;
  color: grey;
  display: block;
  margin-top: 200px;
  font-size: 30px;
}

.filter {
  padding-top: 60px;
}

.pagination {
  border-bottom: 1px solid rgba(224, 224, 224, 1);
}

.inline:last-child {
  display: flex;
  justify-content: center;
}
.inline {
  display: flex;
  align-items: center;
  border-bottom: none !important;
  border-top: 1px solid rgba(224, 224, 224, 1) !important;
}

.modalCard {
  margin: 20px 0;
  display: flex;
  justify-content: space-between;
}

.textField {
  display: flex;
  flex-direction: column;
}
.modalBtn {
  margin-top: 20px;
  display: flex;
  justify-content: space-between;
}

.del {
  padding: 0;
  border: none;
}

.typo {
  margin: 20px 0;
}

.p {
  font-weight: bold;
}

.sox {
  cursor: pointer;
  text-decoration: none;
  color: black;
  font-weight: bold;
}

.sd {
  display: flex;
  justify-content: center;
  align-items: center;
  color: inherit;
  text-decoration: none;
}

.panel {
  width: 80%;
  margin: 50px auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.packs {
  display: flex;
  font-size: 35px;
  font-weight: bold;
}
.glav {
  width: 80%;
  margin: 100px auto 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}
.search {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.str {
  font-weight: bold;
  margin: 10px;
}

.btn {
  margin-top: 6px;
}

.box {
  display: flex;
  align-items: center;
  margin-top: 47px;
}

.span {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  height: 50px;
  margin: 10px;
  border: 1px solid black;
}

.back {
  color: black;
  text-decoration: none;
  margin: 50px auto 0 auto;
  width: 80%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.btnIcon {
  background-color: inherit;
  border: none;
}



.nav{
  text-decoration: none;
  color: inherit;
}


.btnIconGrey {
  color: grey;
}

.modalTitle {
  display: flex;
  justify-content: space-between;
}

.modalClose {
  cursor: pointer;
}

.modal {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.input {
  margin: 15px 0 10px 0;
}

.nova {
  margin: 15px 0;
}

.checkbox {
  margin: 10px 0 15px 0;
}
