.card {
  margin: 200px auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sp {
  margin: 20px 0;
  color: grey;
}

.btn {
  margin-bottom: 30px !important;
}

.back {
  color: black;
  text-decoration: none;
  margin: 50px auto 0 auto;
  width: 80%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.cardForm {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 0 30px 0;
}

.inp {
  width: 90%;
}

.form {
  width: 100%;
}

.check {
  height: 42px;
  position: relative;
  width: 90%;
  margin: 0 auto;
}

.checked {
  position: absolute;
  left: 0;
}

.decor {
  color: black;
  text-decoration: none;
  margin: 0 0 65px 240px;
}

.forgot {
  display: flex;
  flex-direction: column;
}

.already {
  color: grey;
  font-size: 15px;
  margin: 35px 0 15px 0;
  text-decoration: none;
  font-weight: bold;
}

.instruction {
  color: grey;
  font-size: 14px;
  padding: 30px;
}

.up {
  color: blue;
  font-size: 20px;
}
